.photo-icon2 {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.were-here-to,
.your-workflow {
  margin: 0;
}
.headline5 {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 500;
  font-family: inherit;
  display: inline-block;
  max-width: 100%;
}
.headline4 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.together,
.your-docs-projects {
  margin: 0;
}
.description10 {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  display: inline-block;
  max-width: 100%;
}
.text2 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.description9 {
  align-self: stretch;
  font-size: var(--font-size-5xl);
}
.description9,
.headline-description,
.photo-instance {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.headline-description {
  align-self: stretch;
  gap: var(--gap-13xl);
  z-index: 1;
}
.photo-instance {
  flex: 1;
  padding: var(--padding-77xl) var(--padding-45xl) var(--padding-137xl);
  box-sizing: border-box;
  position: relative;
  gap: var(--gap-235xl);
  min-width: 468px;
  text-align: left;
  font-size: var(--font-size-31xl);
  color: var(--color-white);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 1050px) {
  .headline5 {
    font-size: var(--font-size-21xl);
  }
  .photo-instance {
    padding-top: var(--padding-43xl);
    padding-bottom: var(--padding-82xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .description9,
  .headline-description {
    gap: var(--gap-base);
  }
  .photo-instance {
    gap: var(--gap-108xl);
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
    min-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  .headline5 {
    font-size: var(--font-size-11xl);
  }
  .description10 {
    font-size: var(--font-size-lgi);
  }
  .photo-instance {
    gap: var(--gap-44xl);
    padding-top: var(--padding-21xl);
    padding-bottom: var(--padding-47xl);
    box-sizing: border-box;
  }
}
