/* App.css */

/* Header styles */
header {
    background-color: #000000;
    color: #fff;
    padding: 5px 0;
  }
  
  /* Navigation styles */
  nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  nav ul li {
    margin-right: 20px;
  }
  
  nav ul li:last-child {
    margin-right: 0;
  }
  
  nav ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 18px;
  }
  
  /* Content styles */
  .content {
    padding: 0px;
  }
  