.headline3 {
  margin: 0;
  width: 449px;
  position: relative;
  font-size: inherit;
  font-weight: 500;
  font-family: inherit;
  display: inline-block;
  max-width: 113%;
  flex-shrink: 0;
}
.headline2 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0;
  box-sizing: border-box;
  max-width: 100%;
}
.button {
  position: relative;
  font-size: var(--font-size-mid);
  font-weight: 500;
  font-family: var(--font-poppins);
  color: var(--color-white);
  text-align: left;
  display: inline-block;
  min-width: 79px;
}
.frame3,
.frame4,
.text-button {
  align-self: stretch;
  display: flex;
}
.text-button {
  cursor: pointer;
  border: 0;
  padding: var(--padding-mid) var(--padding-xl);
  background-color: var(--color-black);
  border-radius: var(--br-3xs);
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.text-button:hover {
  background-color: var(--color-darkslategray-100);
}
.frame3,
.frame4 {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
  max-width: 100%;
}
.frame3 {
  gap: var(--gap-45xl);
}
.arrow-left-24px1 {
  width: 24px;
  height: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.arrow-left-24px-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-12xs) 0 0;
}
.back1 {
  position: relative;
  font-weight: 500;
  display: inline-block;
  min-width: 42px;
}
.back-button1,
.forgot-password-inner,
.frame-group {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.back-button1 {
  flex-direction: row;
  padding: 0 var(--padding-xl) 0 0;
  gap: var(--gap-9xs);
  text-align: left;
  font-size: var(--font-size-mid);
}
.forgot-password-inner,
.frame-group {
  flex-direction: column;
  max-width: 100%;
}
.frame-group {
  align-self: stretch;
  gap: 345px;
}
.forgot-password-inner {
  width: 400px;
  padding: var(--padding-77xl) 0 0;
  box-sizing: border-box;
  min-width: 400px;
}
.photo-icon {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.forgot-password,
.photo-parent {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}
.photo-parent {
  flex: 1;
  flex-direction: column;
  padding: var(--padding-77xl) var(--padding-45xl) var(--padding-137xl);
  gap: var(--gap-235xl);
  min-width: 468px;
  max-width: 100%;
}
.forgot-password {
  width: 100%;
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: row;
  padding: 0 0 0 var(--padding-141xl);
  gap: var(--gap-141xl);
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  font-size: var(--font-size-31xl);
  color: var(--color-black);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 1125px) {
  .forgot-password-inner {
    flex: 1;
  }
}
@media screen and (max-width: 1050px) {
  .headline3 {
    font-size: var(--font-size-21xl);
  }
  .forgot-password-inner,
  .photo-parent {
    padding-top: var(--padding-43xl);
    box-sizing: border-box;
  }
  .photo-parent {
    padding-bottom: var(--padding-82xl);
  }
  .forgot-password {
    flex-wrap: wrap;
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .forgot-password-inner,
  .photo-parent {
    min-width: 100%;
  }
  .photo-parent {
    gap: var(--gap-108xl);
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
  .forgot-password {
    gap: var(--gap-61xl);
  }
}
@media screen and (max-width: 450px) {
  .headline3 {
    font-size: var(--font-size-11xl);
  }
  .frame3 {
    gap: var(--gap-13xl);
  }
  .frame-group {
    gap: 172px;
  }
  .forgot-password-inner,
  .photo-parent {
    padding-top: var(--padding-21xl);
    box-sizing: border-box;
  }
  .photo-parent {
    gap: var(--gap-44xl);
    padding-bottom: var(--padding-47xl);
  }
  .forgot-password {
    gap: var(--gap-21xl);
  }
}
