.f-r-a-m-e {
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 156.5px 0 0;
  box-sizing: border-box;
  min-width: 400px;
  max-width: 100%;
}
.login-1,
.login-1-child {
  position: relative;
  overflow: hidden;
}
.login-1-child {
  height: 100px;
  width: 100px;
  flex-shrink: 0;
  display: none;
}
.login-1 {
  width: 100%;
  background-color: var(--color-white);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 0 var(--padding-137xl);
  box-sizing: border-box;
  gap: var(--gap-145xl);
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1125px) {
  .f-r-a-m-e {
    flex: 1;
  }
}
@media screen and (max-width: 1050px) {
  .f-r-a-m-e,
  .login-1 {
    box-sizing: border-box;
  }
  .f-r-a-m-e {
    padding-top: 102px;
  }
  .login-1 {
    flex-wrap: wrap;
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    padding-bottom: var(--padding-xl);
  }
}
@media screen and (max-width: 750px) {
  .f-r-a-m-e {
    min-width: 100%;
  }
  .login-1 {
    gap: 82px;
  }
}
@media screen and (max-width: 450px) {
  .f-r-a-m-e {
    padding-top: var(--padding-47xl);
    box-sizing: border-box;
  }
  .login-1 {
    gap: 41px;
  }
}
