.button1 {
  position: relative;
  font-size: var(--font-size-mid);
  font-weight: 500;
  font-family: var(--font-poppins);
  color: var(--color-white);
  text-align: left;
}
.text-button1 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-mid) var(--padding-xl);
  background-color: var(--color-black);
  align-self: stretch;
  border-radius: var(--br-3xs);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
}
.text-button1:hover {
  background-color: var(--color-darkslategray-100);
}
.description6 {
  position: relative;
  font-weight: 500;
}
.description5 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.link {
  position: relative;
  text-decoration: underline;
  font-weight: 500;
  display: inline-block;
  min-width: 98px;
}
.left-aligned-link-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: var(--color-black);
}
.description-link,
.frame5 {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
}
.description-link {
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-126xl) 0 0;
  gap: var(--gap-9xs);
}
.frame5 {
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-base);
  text-align: left;
  font-size: var(--font-size-smi);
  color: var(--color-gray);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 450px) {
  .description-link {
    flex-wrap: wrap;
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
