.photo-icon3,
.photo-instance-child {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.photo-icon3 {
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.photo-instance-child {
  background: linear-gradient(
    180deg,
    #000,
    rgba(108, 108, 108, 0.5) 36%,
    rgba(255, 255, 255, 0)
  );
  z-index: 1;
}
.welcome-to-your {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 500;
  font-family: inherit;
  display: inline-block;
  max-width: 100%;
}
.headline12 {
  width: 592px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 110%;
  flex-shrink: 0;
}
.description14 {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  display: inline-block;
  max-width: 100%;
}
.description13,
.text6 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.text6 {
  align-self: stretch;
  flex-direction: row;
  max-width: 100%;
}
.description13 {
  width: 592px;
  flex-direction: column;
  max-width: 110%;
  flex-shrink: 0;
  font-size: var(--font-size-5xl);
}
.headline-description-wrapper,
.headline-description2 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.headline-description2 {
  width: 541px;
  flex-direction: column;
  padding: 0;
  z-index: 2;
}
.headline-description-wrapper {
  flex-direction: row;
  padding: 0 var(--padding-9xs);
}
.arrow-left-32px2,
.arrow-right-32px2 {
  height: 32px;
  width: 32px;
  position: relative;
  min-height: 32px;
  z-index: 2;
}
.arrow-right-32px2 {
  object-fit: contain;
}
.arrow-left-32px-container {
  width: 96px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.photo-instance1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-39xl) var(--padding-45xl) var(--padding-15xl);
  box-sizing: border-box;
  position: relative;
  gap: var(--gap-631xl);
  min-width: 468px;
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-31xl);
  color: var(--color-white);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 1125px) {
  .headline-description2 {
    flex: 1;
  }
}
@media screen and (max-width: 1050px) {
  .welcome-to-your {
    font-size: var(--font-size-21xl);
  }
  .photo-instance1 {
    padding-top: var(--padding-19xl);
    padding-bottom: var(--padding-3xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .description13 {
    gap: var(--gap-base);
  }
  .photo-instance1 {
    gap: var(--gap-306xl);
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
    min-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  .welcome-to-your {
    font-size: var(--font-size-11xl);
  }
  .description14 {
    font-size: var(--font-size-lgi);
  }
  .photo-instance1 {
    gap: var(--gap-143xl);
    padding-top: var(--padding-6xl);
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
  }
}
