.frame-wrapper {
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-77xl) 0 0;
  box-sizing: border-box;
  min-width: 400px;
  max-width: 100%;
}
.photo-icon1 {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.description4 {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  display: inline-block;
  max-width: 100%;
}
.description3,
.text {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.text {
  flex: 1;
}
.description3 {
  align-self: stretch;
  z-index: 1;
}
.arrow-left-32px,
.arrow-right-32px {
  height: 32px;
  width: 32px;
  position: relative;
  min-height: 32px;
  z-index: 1;
}
.arrow-right-32px {
  object-fit: contain;
}
.arrow-left-32px-parent {
  width: 96px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.create-account,
.description-parent,
.photo-group {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.description-parent {
  align-self: stretch;
  flex-direction: column;
  gap: var(--gap-38xl);
  max-width: 100%;
}
.create-account,
.photo-group {
  position: relative;
  box-sizing: border-box;
}
.photo-group {
  flex: 1;
  flex-direction: column;
  padding: var(--padding-77xl) var(--padding-45xl) var(--padding-137xl);
  gap: var(--gap-235xl);
  min-width: 468px;
  max-width: 100%;
}
.create-account {
  width: 100%;
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: row;
  padding: 0 0 0 var(--padding-141xl);
  gap: var(--gap-141xl);
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 1125px) {
  .frame-wrapper {
    flex: 1;
  }
}
@media screen and (max-width: 1050px) {
  .frame-wrapper,
  .photo-group {
    padding-top: var(--padding-43xl);
    box-sizing: border-box;
  }
  .photo-group {
    padding-bottom: var(--padding-82xl);
  }
  .create-account {
    flex-wrap: wrap;
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .frame-wrapper {
    min-width: 100%;
  }
  .description3 {
    gap: var(--gap-base);
  }
  .description-parent {
    gap: var(--gap-9xl);
  }
  .photo-group {
    gap: var(--gap-108xl);
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
    min-width: 100%;
  }
  .create-account {
    gap: var(--gap-61xl);
  }
}
@media screen and (max-width: 450px) {
  .frame-wrapper {
    padding-top: var(--padding-21xl);
    box-sizing: border-box;
  }
  .description4 {
    font-size: var(--font-size-lgi);
  }
  .photo-group {
    gap: var(--gap-44xl);
    padding-top: var(--padding-21xl);
    padding-bottom: var(--padding-47xl);
    box-sizing: border-box;
  }
  .create-account {
    gap: var(--gap-21xl);
  }
}
