.headline9 {
  margin: 0;
  position: relative;
  font-size: var(--font-size-31xl);
  font-weight: 500;
  font-family: var(--font-poppins);
  color: var(--color-black);
  text-align: center;
}
.headline8 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 var(--padding-10xs-5);
}
.input-field5 {
  width: 48px;
  border: 0;
  outline: 0;
  font-weight: 500;
  font-family: var(--font-poppins);
  font-size: var(--font-size-mid);
  background-color: transparent;
  height: 26px;
  position: relative;
  color: var(--color-gray);
  text-align: left;
  display: inline-block;
  padding: 0;
}
.input-field4 {
  align-self: stretch;
  border-radius: var(--br-3xs);
  border: 1px solid var(--color-gray);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-mini) var(--padding-xl);
}
.input-field7 {
  width: 83px;
  border: 0;
  outline: 0;
  font-weight: 500;
  font-family: var(--font-poppins);
  font-size: var(--font-size-mid);
  background-color: transparent;
  height: 26px;
  position: relative;
  color: var(--color-gray);
  text-align: left;
  display: inline-block;
  padding: 0;
}
.frame8,
.input-field6 {
  align-self: stretch;
  display: flex;
  justify-content: flex-start;
}
.input-field6 {
  border-radius: var(--br-3xs);
  border: 1px solid var(--color-gray);
  flex-direction: row;
  align-items: center;
  padding: var(--padding-mini) var(--padding-xl);
}
.frame8 {
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-5xl);
  max-width: 100%;
}
.apple-logo-icon {
  height: 32px;
  width: 32px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.text4 {
  position: relative;
  font-size: var(--font-size-mid);
  font-weight: 500;
  font-family: var(--font-poppins);
  color: var(--color-white);
  text-align: left;
}
.icon-text-button {
  cursor: pointer;
  border: 0;
  padding: var(--padding-sm) var(--padding-xl);
  background-color: var(--color-black);
  align-self: stretch;
  border-radius: var(--br-3xs);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
  white-space: nowrap;
}
.icon-text-button:hover {
  background-color: var(--color-darkslategray-100);
}
.logo-icon {
  height: 32px;
  width: 32px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.sign-up-with {
  position: relative;
  font-size: var(--font-size-mid);
  font-weight: 500;
  font-family: var(--font-poppins);
  color: var(--color-black);
  text-align: left;
}
.icon-text-button-stroke {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-xs) var(--padding-xl);
  background-color: transparent;
  align-self: stretch;
  border-radius: var(--br-3xs);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
  white-space: nowrap;
}
.icon-text-button-stroke:hover {
  background-color: var(--color-darkslategray-200);
  border: 1px solid var(--color-darkslategray-100);
  box-sizing: border-box;
}
.frame7,
.frame9 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.frame7 {
  margin: 0;
  gap: var(--gap-45xl);
  max-width: 100%;
}
@media screen and (max-width: 1050px) {
  .headline9 {
    font-size: var(--font-size-21xl);
  }
}
@media screen and (max-width: 450px) {
  .headline9 {
    font-size: var(--font-size-11xl);
  }
  .frame7 {
    gap: var(--gap-13xl);
  }
}
