.headline1 {
  margin: 0;
  position: relative;
  font-size: inherit;
  font-weight: 500;
  font-family: inherit;
}
.headline {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 8.5px;
}
.description2 {
  position: relative;
  font-weight: 500;
}
.description1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.description-text1 {
  position: relative;
  font-weight: 500;
}
.description-text {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: var(--color-black);
}
.description {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 129px 0 0;
  gap: var(--gap-9xs);
}
.input-field1 {
  width: 47px;
  border: 0;
  outline: 0;
  font-weight: 500;
  font-family: var(--font-poppins);
  font-size: var(--font-size-mid);
  background-color: transparent;
  height: 26px;
  position: relative;
  color: var(--color-gray);
  text-align: left;
  display: inline-block;
  padding: 0;
}
.input-field {
  align-self: stretch;
  border-radius: var(--br-3xs);
  border: 1px solid var(--color-gray);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-mini) var(--padding-xl);
}
.frame2 {
  gap: var(--gap-base);
}
.frame,
.frame1,
.frame2 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame1 {
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--font-size-smi);
  color: var(--color-gray);
}
.frame {
  gap: var(--gap-45xl);
}
.arrow-left-24px {
  width: 24px;
  height: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.arrow-left-24px-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-12xs) 0 0;
}
.back {
  position: relative;
  font-weight: 500;
  display: inline-block;
  min-width: 42px;
}
.back-button {
  flex-direction: row;
  padding: 0 var(--padding-xl) 0 0;
  gap: var(--gap-9xs);
  text-align: left;
  font-size: var(--font-size-mid);
}
.back-button,
.frame-parent,
.password-reset,
.password-reset-inner {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame-parent {
  align-self: stretch;
  flex-direction: column;
  gap: 329px;
}
.password-reset,
.password-reset-inner {
  box-sizing: border-box;
}
.password-reset-inner {
  width: 400px;
  flex-direction: column;
  padding: var(--padding-77xl) 0 0;
  min-width: 400px;
  max-width: 100%;
}
.password-reset {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: row;
  padding: 0 0 0 var(--padding-141xl);
  gap: var(--gap-141xl);
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  font-size: var(--font-size-31xl);
  color: var(--color-black);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 1125px) {
  .password-reset-inner {
    flex: 1;
  }
}
@media screen and (max-width: 1050px) {
  .headline1 {
    font-size: var(--font-size-21xl);
  }
  .password-reset-inner {
    padding-top: var(--padding-43xl);
    box-sizing: border-box;
  }
  .password-reset {
    flex-wrap: wrap;
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .password-reset-inner {
    min-width: 100%;
  }
  .password-reset {
    gap: var(--gap-61xl);
  }
}
@media screen and (max-width: 450px) {
  .headline1 {
    font-size: var(--font-size-11xl);
  }
  .description {
    flex-wrap: wrap;
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .frame {
    gap: var(--gap-13xl);
  }
  .frame-parent {
    gap: var(--gap-145xl);
  }
  .password-reset-inner {
    padding-top: var(--padding-21xl);
    box-sizing: border-box;
  }
  .password-reset {
    gap: var(--gap-21xl);
  }
}
