.description8 {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  display: inline-block;
  max-width: 100%;
}
.description7,
.text1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.text1 {
  flex: 1;
}
.description7 {
  align-self: stretch;
  z-index: 1;
}
.arrow-left-32px1,
.arrow-right-32px1 {
  height: 32px;
  width: 32px;
  position: relative;
  min-height: 32px;
  z-index: 1;
}
.arrow-right-32px1 {
  object-fit: contain;
}
.arrow-left-32px-group {
  width: 96px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.description-group {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-38xl);
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 750px) {
  .description7 {
    gap: var(--gap-base);
  }
  .description-group {
    gap: var(--gap-9xl);
  }
}
@media screen and (max-width: 450px) {
  .description8 {
    font-size: var(--font-size-lgi);
  }
}
